body {
  font-family: Verdana, Consolas, monospace;
  background: #fff;
  color: #444;
}
body:global(.noselect) .mtree * {
  user-select: none;
}
ul {
  padding-left: 1.5em;
  line-height: 1.5em;
  list-style-type: dot;
}
.mtree li {
  cursor: default;
}
.mtree li>span {
  display: inline-block;
}
.mtree .selected>span,
.mtree .selected>pre {
  color: #f00;
  opacity: 0.5;
}
.mtree .moving>span,
.mtree .moving>pre {
  color: #800080;
  opacity: 0.5;
  font-style: italic;
}
.mtree .copying>span,
.mtree .copying>pre {
  color: #800080;
  opacity: 0.5;
}
